.App {
  text-align: center;
}

body {
  background-color: #000000;
}

.jumbotron {
  background-color: #000015;
  text-align: center;
}

.jumbotron p {
  color: #1adbde;
  font-size: 20px;
  font-weight: 600;
}

@font-face {
  font-family: 'Starjedi';
  font-style: normal;
  font-weight: normal;
  src: local('Starjedi'), url('Starjedi.ttf') format('woff');
  }

h1 {
    font-family: 'Starjedi';
    font-weight: 200;
    font-size: 65px;
    color: #ffe300;
}

h1 .rebel {
  color: #cc0003;
}

h1 .empire {
  color:  #ffffff;
}

.char-section {
  background: linear-gradient(to right, #33373b, #444a51);
  padding-top: 10%;
  padding-bottom: 5%;
  text-align: left;
}

.char-name {
  color: #66ab39;
  margin-left: 3%;
}

.btn {
  margin-left: 3%;
}

.btn-info {
  margin-bottom: 5%;
  background-color: #8536ae;
  border-color: #8536ae;
}

.btn-info:hover {
  background-color: #5e0979;
  border-color: #5e0979;
}

ul {
  background-color: #696969;
  width: 30%;
  margin-left: 3%;
  padding-left: 10%;
  padding-right: 2%;
  padding-top: 2%;
  padding-bottom: 2%;
  border-radius: 20px;
}

li {
  list-style: none;
  color: #ffe300;

}

@media (max-width: 1000px) {
  ul {
    width: 50%;
  }
}

@media (max-width: 767px) {
  h1 {
    font-size: 45px;
  }
}

@media (max-width: 510px) {
  h1 {
    font-size: 35px;
  }
}

@media (max-width: 415px) {
  h1 {
    font-size: 27px;
  }

  .jumbotron p {
    font-size: 14px;
  }

  ul {
    width: 100%;
  }

}






